import React from 'react';
    import {Switch, Route} from 'react-router-dom';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from './components/Navbar';
import ProductList from './components/ProductList';
import Cart from './components/cart';
import Details from './components/Details';
import Default from './components/Default';
import Modal from './components/Modal';

function App() {
  return (
      <React.Fragment>
        <Navbar/>
        <Switch>
          <Route exact path="https://shakhzodbek1997.github.io/react_deployment_demo/" component={ProductList}/>
          <Route path="https://shakhzodbek1997.github.io/react_deployment_demo//details" component={Details}/>
          <Route path="https://shakhzodbek1997.github.io/react_deployment_demo//cart" component={Cart}/>
          <Route component={Default}/>
        </Switch>
        <Modal />
      </React.Fragment>
  );
}

export default App;
